@charset "UTF-8";
body {
  font-family: "";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.btn {
  display: inline-block;
  border: solid 1px #fff;
}
.outside {
  text-align: center;
  background-color: #f00;
}
.swiper {
  max-width: 34.8125rem;
  width: 100%;
}
.swiper-pagination.swiper-pagination-bullets.swiper-pagination-horizontal {
  position: absolute;
  width: auto;
  bottom: initial;
  left: 50%;
  transform: translate(-50%, 0%);
}

@media screen and (min-width: 768px) {
  .works__contents--bg {
    width: 70%;
    min-height: 34.375rem;
    height: 100%;
    background-color: #333;
    margin-left: auto;
  }
}
@media screen and (min-width: 768px) {
  .works__contents--box-wrap {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 1.5625rem;
    position: absolute;
    top: 0;
    left: 0;
  }
}
@media screen and (min-width: 768px) {
  .works__contents--box {
    height: 100%;
  }
}
@media screen and (min-width: 768px) {
  .works__contents--wrap {
    position: relative;
  }
}
.works__swiper {
  margin-bottom: 1.5625rem;
}
@media screen and (min-width: 768px) {
  .works__swiper {
    margin-right: 2rem;
    position: relative;
  }
}
@media screen and (min-width: 768px) {
  .works__text-contents--inner {
    display: flex;
    align-items: center;
    height: 100%;
  }
}
@media screen and (min-width: 768px) {
  .works__text-contents--wrap {
    text-align: left;
  }
}